<template>
    <div class="container mt-5">
        <div class="row justify-content-center">
            <form class="col-8 mt-5">
                <div class="form-group">
                    <label for="exampleInputEmail1">Nombre de usuario</label>
                    <input type="text" class="form-control"  v-model="user.nombre" aria-describedby="emailHelp">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Correo Electronico</label>
                    <input type="email" class="form-control"  v-model="user.email" >
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Contraseña</label>
                    <input type="password" class="form-control"  v-model="user.pass">
                </div>
                <button @click="guardarUsuario()" class="btn btn-primary btn-block mt-4 mb-4">Guardar</button>
            </form>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                user:{
                    nombre:'',
                    email:'',
                    pass:'',
                },
            }
        },
        methods:{   
            guardarUsuario(){
                this.axios.post('/nuevo-user', this.user)
                .then( resp => {
                    if(resp.status === 200){
                        this.limpiar()
                        this.$router.push('/user')
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            limpiar(){
                this.user.nombre = ''
                this.user.email = ''
                this.user.pass = ''
            }
         },
         mounted(){
             this.limpiar
         }

    }
</script>

