<template>
    <div class="container">
        <div class="row mt-4 mb-3">
            <button class="col-4 btn  btn-primary mt-3 mb-3" @click="formulario()">Realizar Pedido</button> 
        </div>
        <div class="row justify-content-center">
            <form class="col-8 mb-3" v-if="form">
                <div class="form-group">
                    <label for="exampleInputEmail1">Peso</label>
                    <input type="number" class="form-control" id="peso" v-model="pedido.peso" aria-describedby="emailHelp" placeholder="ingrese el peso en kg">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Volumen</label>
                    <input type="number" class="form-control"  v-model="pedido.volumen" placeholder="Ingrese el volumen en cm3">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Direccion para recoger</label>
                    <input type="text" class="form-control"  v-model="pedido.direccion" placeholder="Direccion">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Direccion destino</label>
                    <input type="text" class="form-control"  v-model="pedido.destino" placeholder="Direccion destino">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Descripcion</label>
                    <input type="text" class="form-control" v-model="pedido.descripcion" placeholder="Descripcion">
                </div>
                <button @click="guardarPaquete()" class="btn btn-outline-success btn-block mt-3">Enviar</button>
            </form>
            <table class="table col-12" v-show="table">
                <thead>
                    <th>id</th>
                    <th>Descripcion</th>
                    <th>destino</th>
                    <th>Peso</th>
                    <th>Volumen</th>
                    <th>Estado</th>
                    <th>Fecha</th>
                    <th>Eliminar</th>
                    <th>Editar</th>
                </thead>
                <tbody>
                    <tr v-for="(paquete, index) in paquetes" :key="index">
                        <td>{{index+1}}</td>
                        <td>{{paquete.descripcion}}</td>
                        <td>{{paquete.destino}}</td>
                        <td>{{paquete.peso}}</td>
                        <td>{{paquete.volumen}}</td>
                        <td>{{paquete.estado}}</td>
                        <td>{{paquete.date}}</td>
                        <td>
                            <button class="btn btn-outline-danger" @click="eliminarPaquete(paquete._id)">Eliminar</button>
                        </td>
                        <td>
                            <button class="btn btn-outline-info" @click="mostrarPaquete(paquete._id)">Editar</button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <form class="col-8 mb-3" v-if="ver_paquete">
                <div class="form-group">
                    <label for="exampleInputEmail1">Peso</label>
                    <input type="number" class="form-control" id="peso" v-model="paquete.peso" aria-describedby="emailHelp" placeholder="ingrese el peso en kg">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Volumen</label>
                    <input type="number" class="form-control"  v-model="paquete.volumen" placeholder="Ingrese el volumen en cm3">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Direccion para recoger</label>
                    <input type="text" class="form-control"  v-model="paquete.direccion" placeholder="Direccion">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Direccion destino</label>
                    <input type="text" class="form-control"  v-model="paquete.destino" placeholder="Direccion destino">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Descripcion</label>
                    <input type="text" class="form-control" v-model="paquete.descripcion" placeholder="Descripcion">
                </div>
                <button @click="editarPaquete(paquete._id)" class="btn btn-outline-success btn-block mt-3">Enviar</button>
            </form>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                form: false,
                table: false,
                ver_paquete: false,
                pedido:{
                    peso:'',
                    volumen:'',
                    direccion:'',
                    destino:'',
                    descripcion:'',
                },
                paquete:{
                    peso:'',
                    volumen:'',
                    direccion:'',
                    destino:'',
                    descripcion:'',
                    _id:'',
                },
                paquetes: [],
            }
        },
        methods:{   
            guardarPaquete(){
                this.axios.post('/nuevo-paquete', this.pedido)
                .then( resp => {
                    alert('Envio solicitado',resp.data)
                }).catch( error => {
                    console.log(error)
                })
            },
            eliminarPaquete(id){
                this.axios.delete('/eliminar-paquete/'+id)
                .then( resp => {
                    this.paquetes = this.paquetes.slice(0, this.paquetes.length)
                    alert('Paquete eliminado')
                    this.mostrarPaquetes()
                }).catch( error => {
                    console.log(error)
                })
            },
            mostrarPaquetes(){
                this.axios.get('/ver-paquetes')
                .then( resp => {
                    this.paquetes = resp.data
                    if(this.paquetes.length === 0){
                        this.table = false
                    }else{
                        this.table = true
                    }
                    console.log(resp.data)
                }).catch( error => {
                    console.log(error)
                })
            },
            editarPaquete(id){
                console.log(this.paquete)
                this.axios.put('/actualizar-paquete/'+id, this.paquete)
                .then( resp => {
                    if(resp.status === 200){
                        alert('editado')
                    }else{
                        alert(resp.status)
                    }
 
                }).catch( error => {
                    console.log(error)
                })
            },
            mostrarPaquete(id){
                this.axios.get('/mostrar-paquete/'+id)
                .then( resp => {
                    this.limpiar()
                    console.log(resp.data)
                    this.paquete._id = resp.data._id
                    this.paquete.peso = resp.data.peso
                    this.paquete.volumen = resp.data.volumen
                    this.paquete.direccion = resp.data.direccion
                    this.paquete.destino = resp.data.destino
                    this.paquete.descripcion = resp.data.descripcion
                    this.ver_paquete = true
                }).catch( error => {
                    console.log(error)
                })
            },
            formulario(){
                this.form = !this.form
                if(this.form === true){
                    this.table = false
                }else{
                    this.table = true
                }
            },
            limpiar(){
                this.paquete.peso = ''
                this.paquete.volume = ''
                this.paquete.direccion = ''
                this.paquete.destino = ''
                this.paquete.descripcion = ''
                this.paquete._id = ''
            }
        },
        mounted() {
            this.mostrarPaquetes()
        }
    }
</script>

