<template>
    <div class="container">
        <div class="row mt-4 mb-3">
            <button class="col-4 btn  btn-outline-primary mt-3 mb-3" @click="formulario()">Realizar Pedido</button> 
        </div>
        <div class="row justify-content-center">
            <form class="col-8 mb-3" v-if="form">
                <div class="form-group">
                    <label for="exampleInputEmail1">Peso</label>
                    <input type="number" class="form-control" id="peso" v-model="pedido.peso" aria-describedby="emailHelp" placeholder="ingrese el peso en kg">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Volumen</label>
                    <input type="number" class="form-control"  v-model="pedido.volumen" placeholder="Ingrese el volumen en cm3">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Direccion para recoger</label>
                    <input type="text" class="form-control"  v-model="pedido.direccion" placeholder="Direccion">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Direccion destino</label>
                    <input type="text" class="form-control"  v-model="pedido.destino" placeholder="Direccion destino">
                </div>
                <div class="form-group">
                    <label for="exampleInputPassword1">Descripcion</label>
                    <input type="text" class="form-control" v-model="pedido.descripcion" placeholder="Descripcion">
                </div>
                <button @click="guardarPaquete()" class="btn btn-outline-success btn-block mt-3">Enviar</button>
            </form>
            <table class="table col-12" v-show="table">
                <thead>
                    <th>id</th>
                    <th>Descripcion</th>
                    <th>destino</th>
                    <th>Peso</th>
                    <th>Volumen</th>
                    <th>Estado</th>
                    <th>Fecha</th>
                    <th>Eliminar</th>
                    <th>Editar</th>
                </thead>
                <tbody>
                    <tr v-for="(paquete, index) in paquetes" :key="index">
                        <td>{{paquete._id}}</td>
                        <td>{{paquete.descripcion}}</td>
                        <td>{{paquete.destino}}</td>
                        <td>{{paquete.peso}}</td>
                        <td>{{paquete.volumen}}</td>
                        <td>{{paquete.estado}}</td>
                        <td>{{paquete.date}}</td>
                        <td>
                            <button class="btn btn-outline-danger" @click="eliminarPaquete(paquete._id)">Eliminar</button>
                        </td>
                        <td>
                            <button class="btn btn-outline-info">Editar</button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'user',
        data(){
            return{
                form: false,
                table: false,
                pedido:{
                    peso:'',
                    volumen:'',
                    direccion:'',
                    destino:'',
                    descripcion:'',
                },
                paquetes:[]
            }
        },
        methods:{   
            guardarPaquete(){
                this.axios.post('/nuevo-paquete', this.pedido)
                .then( resp => {
                    if(resp.status === 200){
                        alert('Paquete guardado')
                    }
                }).catch( error => {
                    console.log(error)
                })
            },
            eliminarPaquete(id){
                this.axios.delete('/eliminar-paquete/'+id)
                .then( resp => {
                    this.paquetes = this.paquetes.slice(0, this.paquetes.length)
                    alert('Paquete eliminado')
                    this.mostrarPaquetes()
                }).catch( error => {
                    console.log(error)
                })
            },
            mostrarPaquetes(){
                this.axios.get('/ver-paquetes')
                .then( resp => {
                    this.paquetes = resp.data
                    if(this.paquetes.length === 0){
                        this.table = false
                    }else{
                        this.table = true
                    }
                    console.log(resp.data)
                }).catch( error => {
                    console.log(error)
                })
            },
            formulario(){
                this.form = !this.form
                if(this.form === true){
                    this.table = false
                }else{
                    this.table = true
                }
            }
         },
        mounted() {
            this.mostrarPaquetes()
        }
    }
</script>

