<template>
    <div class="container mt-5">
        <div class="row justify-content-center">
            <form class="col-8 mt-5">
                <h1 class="mb-3">Login</h1>
                <div class="form-group mt-3">
                    <label for="exampleInputPassword1">Correo Electronico</label>
                    <input type="email" class="form-control"  v-model="login.email" >
                </div>
                <div class="form-group mt-3">
                    <label for="exampleInputPassword1">Contraseña</label>
                    <input type="password" class="form-control"  v-model="login.pass">
                </div>
                <button @click="loginUser()" class="btn btn-success btn-block mt-4 mb-4">Login</button>
            </form>
        </div>
    </div>
</template>

<script>
    export default {
        name:'login-user',
        data(){
            return{
                login:{
                    email:'',
                    pass:'',
                },
            }
        },
        methods:{   
            loginUser(){
                this.axios.post('/login', this.login)
                .then( resp => {
                    if(resp.status === 200){
                         this.$router.push('/user');
                    }else{
                        alert(resp.message)
                    }
                }).catch( error => {
                    alert(error)
                })
            },

         },

    }
</script>

